<script>
import {mapGetters} from "vuex";
import {
  // FieldTimeOutlined,
  // CheckCircleOutlined,
  // SyncOutlined,
  // MinusCircleOutlined,
  CheckOutlined,
  FundOutlined,
  VideoCameraOutlined,
  ScheduleOutlined,
  ProfileOutlined,
} from "@ant-design/icons-vue";
import {errorHandler} from "@/lib/errorHandler";
// import StatisticWindow from "@/components/events/StatisticWindow";

export default {
  name: "SeparateEventsPage",
  props: ["eventId"],
  components: {
    // StatisticWindow,
    // CheckCircleOutlined,
    // SyncOutlined,
    // MinusCircleOutlined,
    // FieldTimeOutlined,
    CheckOutlined,
    FundOutlined,
    VideoCameraOutlined,
    ScheduleOutlined,
    ProfileOutlined,
  },
  computed: {
    ...mapGetters(["currentEvents", "eventPassing", "role"]),
  },
  beforeMount() {
    scroll(0, 0);
    this.$store
        .dispatch("fetchCurrentEvents", this.eventId)
        .then(async (data) => {
          if (data) {
            document.dispatchEvent(
                new CustomEvent("setTitle", {detail: data.title})
            );
            await this.$store.dispatch(
                "fetchEventPassing",
                data.eventPassings.length ? data.eventPassings[0].id : null
            );
          }
        });
  },
  data() {
    return {
      CheckOutlined,
      dateOptions: {
        month: "long",
        day: "numeric",
      },
      viewStatistic: false,
    };
  },
  methods: {
    checkScore(item) {
      if (this.eventPassing) {
        if (item.type === "interview") {
          // let index = this.eventPassing.event.stages
          //     .map((stage) => stage.id)
          //     .indexOf(item.id);
          // if (index !== -1) {
          //   if (
          //       this.eventPassing.event.stages[index].interviewBookedTime &&
          //       this.eventPassing.event.stages[index].interviewBookedTime
          //           .length &&
          //       this.eventPassing.event.stages[index].interviewBookedTime[0]
          //           .description === "" && (this.eventPassing.event.stages[index].interviewBookedTime[0].whenDate &&
          //           new Date(this.eventPassing.event.stages[index].interviewBookedTime[0].whenDate) < new Date())
          //   ) {
          //     return "pass";
          //   } else {
          //     return "score";
          //   }
          // } else {
          //   return null;
          // }
          return "score";
        } else {
          let index = this.eventPassing.testingPassing
              .map((pass) => pass.testingId)
              .indexOf(item.testId);
          if (index !== -1) {
            if (
                this.eventPassing.testingPassing[index].finalScore ||
                this.eventPassing.testingPassing[index].finalScore === 0
            ) {
              return "score";
            } else {
              return "pass";
            }
          }
        }
      } else {
        return null;
      }
    },
    getButtonText(item) {

      if (new Date(item.startDate) > new Date()) {
        return 'Не началось'
      }
      if (item.type === 'form') {
        const index = this.eventPassing && this.eventPassing.testingPassing ? this.eventPassing.testingPassing.map((pass) => pass.testingId).indexOf(item.testId) : -1;
        if (index !== -1) {
          if (this.eventPassing.testingPassing[index].finalScore != null) return 'Заполненo';
          return 'Заполнено';

        }
        return 'Заполнить'
      }

      if (item.type === 'test' || item.type === 'exercise') {
        const index = this.eventPassing && this.eventPassing.testingPassing ? this.eventPassing.testingPassing.map((pass) => pass.testingId).indexOf(item.testId) : -1;
        if (index !== -1) {
          if (this.eventPassing.testingPassing[index].finalScore != null) return 'Пройдено';
          return 'Ожидает проверки'
        }
        return 'Пройти'
      }

      if (item.type === 'interview') {
        if (new Date() > new Date(item.endDate)) {
          return 'Завершено';
        }

        return 'Календарь'

      }

      if (new Date() > new Date(item.endDate)) {
        return 'Не пройдено';
      }
      return ''

    },
    goToStage(stage) {
      let passingId = this.currentEvents.eventPassings.length
          ? this.currentEvents.eventPassings[0].id
          : "null";

      if (new Date() > new Date(stage.endDate)) {
        errorHandler({
          name: "appointment",
          status: 300,
          meta: {
            body: "Этап закончился",
          },
        });
        return;
      }
      if (new Date(stage.startDate) > new Date()) {
        errorHandler({
          name: "appointment",
          status: 300,
          meta: {
            body: "Этап не начался",
          },
        });
        return;
      }
      // if (this.eventPassing?.currentStageNumber && stage.order - this.eventPassing?.currentStageNumber !== 0) {
      //   errorHandler({
      //     name: "appointment",
      //     status: 300,
      //     meta: {
      //       body: "Вы не прошли предыдущий этап",
      //     },
      //   });
      //   return;
      // } else if (!this.eventPassing && stage.order > 1) {
      //   errorHandler({
      //     name: "appointment",
      //     status: 300,
      //     meta: {
      //       body: "Вы не прошли предыдущий этап",
      //     },
      //   });
      //   return;
      // }


      if ((this.eventPassing &&
              this.eventPassing.testingPassing &&
              this.eventPassing.testingPassing
                  .map((pass) => pass.testingId)
                  .indexOf(stage.testId) !== -1 &&
              stage.type !== "form") ||
          (stage.type === "form" &&
              this.eventPassing &&
              this.eventPassing.testingPassing &&
              this.eventPassing.testingPassing
                  .map((pass) => pass.testingId)
                  .indexOf(stage.testId) !== -1 &&
              this.eventPassing.testingPassing[
                  this.eventPassing.testingPassing
                      .map((pass) => pass.testingId)
                      .indexOf(stage.testId)
                  ].finalScore != null)
      ) {
        errorHandler({
          name: "appointment",
          status: 300,
          meta: {
            body: "Вы уже прошли этот этап",
          },
        });
        return;
      }
      switch (stage.type) {
        case "interview":
          this.$router.push({
            name: "interview",
            params: {
              interviewId: stage.interviewId,
              eventId: this.currentEvents.id,
              role: "user",
              passingId,
            },
          });
          break;
        case "test":
          this.$router.push({
            name: "testing",
            params: {
              testingId: stage.testId,
              eventId: this.currentEvents.id,
              role: "user",
              passingId,
              score: "null",
            },
          });
          break;
        case "form":
          this.$router.push({
            name: "form",
            params: {
              formId: stage.testId,
              eventId: this.currentEvents.id,
              role: "user",
              passingId,
            },
          });
          break;
        case "exercise":
          this.$router.push({
            name: "exercise",
            params: {
              exerciseId: stage.testId,
              eventId: this.currentEvents.id,
              role: "user",
              passingId,
            },
          });
          break;
      }
    },
  },
};
</script>

