<template>
  <a-card class="card">
    <!-- TODO: убрать mb, он только для презентации -->
    <template #cover>
      <div class="card-tags">
        <a-tag color="default" code class="card-teg">
          <calendar-outlined/>
          {{
            new Date(
                rater ? rater.startDate : event.startDate
            ).toLocaleDateString("ru-RU", dateOptions) +
            " - " +
            new Date(rater ? rater.endDate : event.endDate).toLocaleDateString(
                "ru-RU",
                dateOptions
            )
          }}
        </a-tag>
        <a-tag color="default" v-if="place" code class="card-teg">
          <environment-outlined/>
          {{ place }}
        </a-tag>
        <a-tag color="default" v-if="target" code class="card-teg">
          <team-outlined/>
          {{ target }}
        </a-tag>
      </div>
      <img alt="example" class="prevertering" :src="event.previewUrl"/>
      <div class="info" v-if="false">
        <div class="info-element">
          <img src="../../assets/team.svg" alt="" class="info-element__img"/>
          <p class="info-element__text">20 участников</p>
        </div>
        <div class="info-element">
          <img
              src="../../assets/calendar.svg"
              alt=""
              class="info-element__img"
          />
          <p class="info-element__text">25-26 февраля</p>
        </div>
        <div class="info-element">
          <img src="../../assets/place.svg" alt="" class="info-element__img"/>
          <p class="info-element__text">Москва</p>
        </div>
      </div>
    </template>
    <a-card-meta class="card-info">
      <template #description>
        <div class="description">
          <div class="description-left">
            <div class="description-caption">
              <a-tooltip
                  :title="
                  event.title +
                  (rater
                    ? ' (' +
                      (rater.type === 'test'
                        ? 'Тестирование'
                        : rater.type === 'exercise'
                        ? 'Практическое задание'
                        : rater.type === 'form'
                        ? 'Анкета'
                        : rater.type === 'interview'
                        ? 'Видео-интервью'
                        : '') +
                      ')'
                    : '')
                "
              >
                <a-typography-title :level="5" style="margin-bottom: -2px">
                  {{
                    textSymbolLimit(event.title, TITLE_LIMIT) +
                    (rater
                        ? " (" +
                        (rater.type === "test"
                            ? "Тестирование"
                            : rater.type === "exercise"
                                ? "Практическое задание"
                                : rater.type === "form"
                                    ? "Анкета"
                                    : rater.type === "interview"
                                        ? "Видео-интервью"
                                        : "") +
                        ")"
                        : "")
                  }}
                  <a-tag
                      color="success"
                      v-if="
                      event.currentStageNumber !== null &&
                      event.currentStageNumber === -1
                    "
                      style="border-radius: 18px"
                  >
                    Пройдено
                  </a-tag>
                  <a-tag
                      color="processing"
                      v-if="
                      event.currentStageNumber !== null &&
                      event.currentStageNumber >= 1 &&
                      new Date() < new Date(event.endDate)
                    "
                      style="border-radius: 18px"
                  >
                    В процессе
                  </a-tag>
                  <a-tag
                      v-if="
                      event.currentStageNumber !== null &&
                      event.currentStageNumber >= 1 &&
                      new Date() > new Date(event.endDate)
                    "
                      style="border-radius: 18px"
                  >
                    <template #icon>
                      <minus-circle-outlined/>
                    </template>
                    Не завершено
                  </a-tag>
                  <a-tag
                      v-if="
                      event.currentStageNumber == null &&
                      new Date() <
                        new Date(rater ? rater.startDate : event.startDate)
                    "
                      style="border-radius: 18px"
                  >
                    Не началось
                  </a-tag>
                  <a-tag
                      v-if="
                      event.currentStageNumber == null &&
                      new Date() >
                        new Date(rater ? rater.startDate : event.startDate) &&
                      new Date() <
                        new Date(rater ? rater.endDate : event.endDate)
                    "
                      style="border-radius: 18px"
                      color="cyan"
                  >
                    Идет
                  </a-tag>
                  <a-tag
                      v-if="
                      event.currentStageNumber == null &&
                      new Date() >
                        new Date(rater ? rater.endDate : event.endDate)
                    "
                      style="border-radius: 18px"
                      color="red"
                  >
                    Завершено
                  </a-tag>
                </a-typography-title>
              </a-tooltip>
            </div>
            <h4 v-if="rater && rater.type !== 'interview'">
              Всего прохождений
              {{ passing ? passing._count.testingPassing : 0 }}
            </h4>
            <pre class="text">{{
                textSymbolLimit(
                    event.description,
                    viewFullDescription
                        ? event.description?.length
                        : DESCRIPTION_LIMIT
                )
              }}</pre>
            <button
                v-if="event.description?.length >= DESCRIPTION_LIMIT"
                class="show-more"
                @click="viewFullDescription = !viewFullDescription"
            >
              {{ viewFullDescription ? "Скрыть" : "Показать полностью" }}
            </button>
          </div>
          <div class="description-right">
            <a-button
                shape="round"
                type="primary"
                class="blue-buttons"
                v-if="rater"
                @click="raterEvent"
            >
              Оценить
            </a-button>
            <a-button
                shape="round"
                type="primary"
                class="blue-buttons"
                v-else-if="admin"
                @click="$emit('edit', event)"
            >
              <template #icon>
                <edit-outlined/>
              </template>
              Редактировать
            </a-button>
            <a-button
                shape="round"
                type="primary"
                class="blue-buttons"
                v-else
                @click="
                $router.push({
                  name: 'separateEvent',
                  params: { eventId: event.id },
                })
              "
            >
              {{
                stageNumber == null && !admin ? "Участвовать" : "Просмотреть"
              }}
            </a-button>
            <a-button
                shape="round"
                v-if="statistic"
                type="primary"
                class="blue-buttons"
                @click="openStatistic"
            >
              Посмотреть статистику
            </a-button>
          </div>
        </div>
      </template>
    </a-card-meta>
  </a-card>
</template>

<script>
import {
  MinusCircleOutlined,
  EditOutlined,
  CalendarOutlined,
  TeamOutlined,
  EnvironmentOutlined,
} from "@ant-design/icons-vue";
import {textSymbolLimit} from "@/lib/textSymbolLimit";

export default {
  name: "EventCard",
  props: ["event", "admin", "rater", "statistic", "stageNumber", "passing"],
  components: {
    MinusCircleOutlined,
    EditOutlined,
    CalendarOutlined,
    TeamOutlined,
    EnvironmentOutlined,
  },
  data() {
    return {
      place: null, target: null,
      dateOptions: {
        month: "long",
        day: "numeric",
      },
      viewFullDescription: false,
      DESCRIPTION_LIMIT: 300,
      TITLE_LIMIT: 75,
    };
  },
  methods: {
    textSymbolLimit,
    raterEvent() {
      if (this.rater.type === "interview") {
        this.$router.push({
          name: "interview",
          params: {
            eventId: this.event.id,
            passingId: "null",
            interviewId: this.rater.id,
            role: "rater",
          },
        });
      } else {
        this.$emit("viewUsers", {
          stageId: this.rater.id,
          testId: this.rater.testId,
          type: this.rater.type,
        });
      }
    },
    openStatistic() {
      this.$store.dispatch("fetchCurrentEvents", this.event.id).then((data) => {
        if (data.id) {
          this.$emit("openStatistic");
        }
      });
    },
  },
  beforeMount() {
    if(this.event.additionalData){
      const additionalData = JSON.parse(this.event.additionalData);
      this.place = additionalData?.place
      this.target = additionalData?.target
    }
  }
};
</script>

